.popular-sec {
	&__content {
		display: grid;
		grid-template-columns: repeat(3, 1fr);
		gap: map-get($space, l5);

		@include rwd-df(md) {
			grid-template-columns: repeat(2, 1fr);
		}

		@include rwd-df(sm) {
			gap: map-get($space, l2);
		}

		@include rwd-df(xs) {
			grid-template-columns: repeat(1, 1fr);

			gap: map-get($space, l2);
		}

		.popular-card {
			@include rwd-df(xs) {
				width: 75%;
				margin-left: auto;
				margin-right: auto;
			}

			@include rwd-only(xxs) {
				width: 100%;
			}
		}
	}
}
