.special-sec {
	&__content {
		display: grid;
		grid-template-columns: repeat(4, 1fr);
		grid-template-rows: repeat(2, 25rem);
		gap: map-get($space, l2);
		object-fit: cover;

		@include rwd-df(md) {
			grid-template-columns: repeat(2, 1fr);
			grid-template-rows: repeat(4, 25rem);
		}

		@include rwd-df(xs) {
			grid-template-columns: repeat(1, 1fr);
			grid-template-rows: repeat(4, 35rem);
		}

		& > div:nth-child(1) {
			grid-column: span 2;
			grid-row: span 2;

			@include rwd-df(xs) {
				grid-area: auto;
			}
		}

		& > div:nth-child(2) {
			grid-column: span 2;

			@include rwd-df(md) {
				grid-row: 4;
			}
			@include rwd-df(xs) {
				grid-area: auto;
			}
		}

		& > div:nth-child(3) {
		}
		& > div:nth-child(4) {
		}
	}
}
