// /*
// **  define styling for element components with specified with classe main footer.
// */

.footer {
	background-color: rgba(map-get($color-primary, d5), 0.9);
	padding: map-get($space, l5) map-get($space, l2);

	&__content {
		display: grid;
		grid-template-columns: 1fr 3fr 2fr;
		gap: map-get($space, l3);

		@include rwd-df(sm) {
			grid-template-columns: 1fr 3fr;
			gap: map-get($space, l4);
		}

		@include rwd-df(xs) {
			grid-template-columns: 1fr;
			gap: map-get($space, l4);
		}
	}
	&__column {
		&--nav {
			@include rwd-df(xs) {
				grid-row: 1;
			}
		}
		&--about {
			@include rwd-df(sm) {
				grid-column: 1 / -1;
				grid-row: 1 / 2;
			}

			@include rwd-df(xs) {
				grid-column: 1;
				grid-row: 2;
			}
		}
		&--social {
			@include rwd-df(xs) {
				grid-row: 3;
			}
		}
	}
	&__title {
		color: map-get($color-primary, main);
		font-size: map-get($font-size, l3);
		@extend .u-heading-font;
		margin-bottom: map-get($space, l2);
		text-transform: capitalize;
		@include rwd-df(xs) {
			margin-bottom: map-get($space, main);
		}
	}
	&__text {
		font-size: map-get($font-size, main);
		color: map-get($color-primary, l4);
		&:not(:last-child) {
			margin-bottom: map-get($space, main);
		}
	}

	&__copy-right {
		// @extend .u-heading-font;
		font-style: italic;
		font-size: map-get($font-size, s2);
		color: map-get($color-primary, l3);
		margin-top: map-get($space, l1);
	}

	&__nav-list {
		display: flex;
		flex-direction: column;
		gap: map-get($space, l1);
		&__item {
			&__link {
				&:link,
				&:visited {
					text-transform: uppercase;
					color: map-get($color-primary, l4);
					font-weight: map-get($font-weight, l2);
					transition: map-get($time, main);
				}
				&:hover,
				&:active {
					color: map-get($color-primary, main);
				}
			}
		}
	}
}
