.menus-sec {
	&__content {
		display: grid;
		grid-template-columns: 22rem 4fr;
		gap: map-get($space, l3);
		align-items: start;

		@include rwd-df(sm) {
			grid-template-columns: 1fr;
			justify-items: center;
		}
	}

	&__list {
		@include rwd-df(sm) {
			width: 22rem;
		}

		background-image: url('../../assets/img/menus/vertical-menu-bg.png');
		background-position: center center;
		background-size: 100% 100%;
		padding: map-get($space, main) 0;
		display: flex;
		flex-direction: column;
		align-items: center;
		gap: map-get($space, s1);

		&__item {
			&__link {
				&:link,
				&:visited {
					color: map-get($color-primary, l4);
					text-transform: uppercase;
					transition: map-get($time, main);
					font-weight: map-get($font-weight, l2);
				}
				&:hover,
				&:active {
					color: map-get($color-primary, main);
				}
			}
		}
	}

	&__coffee--cards-list {
		display: flex;
		flex-direction: column;
		gap: map-get($space, l2);

		@include rwd-df(sm) {
			width: 75%;
		}

		@include rwd-df(xs) {
			width: 90%;
		}

		@include rwd-only(xxs) {
			width: 100%;
		}
	}
}
