/*
**  define the style for the body in the page
*/
body {
	font-family: map-get($font-family, secondary), sans-serif;
	font-size: map-get($font-size, main);
	font-weight: map-get($font-weight, main);
	color: map-get($color-grey, main);
	background-color: map-get($color-grey, l4);
}

/*
**  define the style for the primary heading in the page  =>heading-primary
*/
.primary-heading {
	@extend .u-heading-font;
	font-size: map-get($font-size, l5);
	display: flex;
	align-items: baseline;
	gap: map-get($space, main);
	color: map-get($color-primary, main);
	margin-bottom: map-get($space, l4);
	text-transform: capitalize;

	@include rwd-df(sm) {
		justify-content: center;
	}

	@include rwd-df(xs) {
		font-size: map-get($font-size, l4);
		gap: map-get($space, s2);
	}

	&__image {
		@include squareSize(map-get($space, l2));
	}

	&::after {
		content: '';
		display: block;
		flex-grow: 1;
		height: map-get($space, s7);
		border-radius: map-get($space, main);
		background-color: map-get($color-primary, main);

		@include rwd-df(md) {
		}
		@include rwd-df(sm) {
			display: none;
		}
	}
}

/*
**  define the style for the secondary heading in the page  =>heading-secondary
*/
.secondary-heading {
	font-size: map-get($font-size, l3);
	color: map-get($color-grey, main);
	font-weight: map-get($font-weight, l3);
	text-transform: uppercase;
	margin-bottom: map-get($space, s1);

	@include rwd-df(xs) {
		font-size: map-get($font-size, l2);
	}
}

/*
**  define the style for the paragraph  in the page
*/
.paragraph {
	font-size: map-get($font-size, l1);
	color: map-get($color-grey, l1);
	// line-height: map-get($line-height, main);

	@include rwd-df(xs) {
		font-size: map-get($font-size, main);
	}

	&:not(:last-child) {
		margin-bottom: map-get($space, l2);
	}
}

/*
**  define the style for the sections  in the page
*/
.sec {
	padding: map-get($space, l4) 0;

	@include rwd-df(sm) {
		padding: map-get($space, l2) 0;
	}
}
