/*
**  Define font "Damion" Locally
*/
//Local Font ==> font-family: Damion , cursive  , sans-serif ;
@font-face {
	font-family: 'Damion';
	src: url('../../assets/fonts/Damion/Damion.eot'); /* IE9 Compat Modes */
	src: url('../../assets/fonts/Damion/Damion.eot?#iefix')
			format('embedded-opentype'),
		/* IE6-IE8 */ url('../../assets/fonts/Damion/Damion.woff2') format('woff2'),
		/* Super Modern Browsers */ url('../../assets/fonts/Damion/Damion.woff')
			format('woff'),
		/* Pretty Modern Browsers */ url('../../assets/fonts/Damion/Damion.ttf')
			format('truetype'),
		/* Safari, Android, iOS */
			url('../../assets/fonts/Damion/Damion.svg#svgFontName') format('svg'); /* Legacy iOS */
	font-weight: normal;
	font-style: normal;
	font-display: swap;
}

