.preloader {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;

	display: flex;
	justify-content: center;
	align-items: center;

	z-index: map-get($z-index, xxl3);
	color: map-get($color-primary, main);
	background-color: map-get($color-grey, l4);
	visibility: visible;
	opacity: 1;
	transition: map-get($time, main);

	&.hidden {
		visibility: hidden;
		opacity: 0;
		pointer-events: none;
		z-index: -1;
	}

	div.content {
		.loader-frame {
			@include squareSize(map-get($space, l7) * 2);
			margin: auto;
			position: relative;
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;
			text-align: center;

			.loader1 {
				position: absolute;
				border-radius: 50%;
				@include squareSize(map-get($space, l7) * 2);
				border-top: map-get($space, s6) solid map-get($color-grey, l1);
				border-bottom: map-get($space, s6) solid map-get($color-grey, main);
				animation: clockwisespin 4s linear 3;
			}

			.circle {
				position: absolute;
				@include squareSize(map-get($space, l7) * 2);
				border: map-get($space, s8) solid map-get($color-grey, main);
				border-radius: 50%;
				display: block;
			}

			.wellcome {
				@extend .u-heading-font;
				font-size: map-get($font-size, l4);
				@include rwd-df(sm) {
					font-size: map-get($font-size, l3);
				}
			}

			.cafe {
				@extend .u-heading-font;
				font-size: map-get($font-size, l7);

				@include rwd-df(sm) {
					font-size: map-get($font-size, l6);
				}
			}
		}
	}
}

@keyframes clockwisespin {
	from {
		transform: rotate(0deg);
	}
	to {
		transform: rotate(360deg);
	}
}
