/*
**  define styling for element component with specified with classe  main header.
*/
.header {
	position: relative;
	background-color: map-get($color-primary, d4);
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: map-get($space, s2) map-get($space, l2);

	&.sticky {
		position: fixed;
		top: 0;
		left: 0;
		right: 0;
		opacity: 0.9;
		z-index: map-get($z-index, xxl2);
	}

	&__logo {
		display: flex;
		align-items: baseline;
		gap: map-get($space, s3);

		&:link,
		&:visited {
			color: map-get($color-primary, main);
			@extend .u-heading-font;
			font-size: map-get($font-size, l5);
		}

		&__image {
			display: inline;
			@include squareSize(map-get($space, l2));
		}
	}

	&__nav {
		&__list {
			display: flex;
			align-items: center;
			gap: map-get($space, l3);

			@include rwd-df(md) {
				position: absolute;
				top: 100%;
				#{$end}: map-get($space, l2);
				flex-direction: column;
				background-color: map-get($color-primary, d4);
				padding: map-get($space, l1) map-get($space, l2);
				border-radius: map-get($space, s5);
				gap: map-get($space, main);
				transform: scaleY(0);
				transition: transform map-get($time, main);
				transform-origin: top center;
			}

			&__item {
				&__link {
					&:link,
					&:visited {
						color: map-get($color-primary, l4);
						font-size: map-get($font-size, l2);
						text-transform: uppercase;
						font-weight: map-get($font-weight, l2);
						transition: all map-get($time, main);
					}

					&:hover,
					&:active,
					&.active {
						color: map-get($color-primary, main);
					}
				}
			}
		}

		&.show &__list {
			@include rwd-df(md) {
				transform: scaleY(1);
			}
		}

		&__toggle-btn {
			display: none;

			@include rwd-df(md) {
				display: flex;
				flex-direction: column;
				gap: map-get($space, s2);
				transition: all map-get($time, s2);
			}

			&__bar {
				display: block;
				width: map-get($space, l3);
				height: map-get($space, s6);
				background-color: map-get($color-primary, l4);
				border-radius: map-get($space, main);
				transition: all map-get($time, main);
			}

			&:hover {
				gap: map-get($space, s4);
			}

			&:hover &__bar {
				background-color: map-get($color-primary, main);
			}
		}
	}
}
