.btn {
	&,
	&:link,
	&:visited {
		background-image: url('../../assets/img/button/brown-button.png');
		@include backgroundImage;
		display: inline-flex;
		justify-content: center;
		align-items: center;
		width: map-get($space, l6) * 2;
		height: map-get($space, l3);
		font-size: map-get($font-size, main);
		color: map-get($color-primary, main);
		// padding: map-get($space, s1) map-get($space, l1);
		border-radius: map-get($space, s1);
		font-weight: map-get($font-weight, l2);
		text-transform: uppercase;
		transition: all map-get($time, main);
	}

	&:hover,
	&:active {
		transform: translateY(-3px);
		color: map-get($color-primary, l4);
		@include boxShadow(main);
	}
}
