.daily-sec {
	&__content {
		display: grid;
		grid-template-columns: 2fr 3fr;
		gap: map-get($space, l6);

		@include rwd-df(md) {
			grid-template-columns: 1fr;
			justify-items: center;
			gap: map-get($space, l2);
		}
	}
	&__image {
		border-radius: map-get($space, main);
		@include boxShadow(main);
		@include rwd-df(md) {
			width: 75%;
		}

		@include rwd-df(xs) {
			width: 90%;
		}
	}
	&__list {
		display: flex;
		flex-direction: column;
		gap: map-get($space, s2);
		color: map-get($color-grey, d1);
		margin-#{$start}: map-get($space, l4);

		@include rwd-df(xs) {
			margin-#{$start}: map-get($space, main);
		}

		margin-bottom: map-get($space, l2);

		counter-reset: listNumber;

		& > li::before {
			content: counter(listNumber) '.';
			counter-increment: listNumber;
			margin-#{$end}: map-get($space, s3);
			color: map-get($color-primary, main);
			font-weight: map-get($font-weight, l2);
		}
	}
}
