.hero-sec {
	background-color: rgba(map-get($color-primary, d5), 0.85);
	// margin-bottom: map-get($space, l5) * -1;

	&__light-spots {
		width: fit-content;
		margin: 0 auto;
		display: flex;
		gap: map-get($space, l4);
		&__image {
			@include rwd-df(lg) {
				height: map-get($space, l7) * 2;
			}

			@include rwd-df(md) {
				height: map-get($space, l5) * 2;
			}

			@include rwd-df(xs) {
				height: map-get($space, l6) * 2;
			}

			&:first-child,
			&:last-child {
				@include rwd-df(xs) {
					display: none;
				}
			}
		}
	}

	&__text {
		display: flex;
		flex-direction: column;
		align-items: center;
		gap: map-get($space, l1);
		margin: auto;
		width: 65%;
		@include rwd-df(md) {
			width: 90%;
		}

		@include rwd-df(xs) {
			width: 100%;
		}
	}

	&__sub-title {
		font-size: map-get($font-size, l6);
		font-weight: map-get($font-weight, l3);
		@extend .u-heading-font;
		color: map-get($color-primary, l5);
		text-transform: capitalize;
		display: flex;
		align-items: baseline;
		justify-content: center;
		gap: map-get($space, l2);

		@include rwd-df(xs) {
			font-size: map-get($font-size, l5);
			gap: map-get($space, main);
		}

		&::before,
		&::after {
			content: '';
			display: block;
			width: map-get($space, l4);
			height: map-get($space, main);
			background-image: url('../../assets/img/hero/header-line.png');
			@include backgroundImage;

			@include rwd-df(xs) {
				width: map-get($space, l2);
				height: map-get($space, s1);
			}
		}
	}

	&__main-title {
		font-size: map-get($font-size, l7);
		font-weight: map-get($font-weight, l3);
		text-transform: uppercase;
		color: map-get($color-primary, main);
		letter-spacing: map-get($space, s1);

		@include rwd-df(xs) {
			font-size: map-get($font-size, l5);
			letter-spacing: map-get($space, s4);
		}
	}

	&__description {
		color: map-get($color-primary, l4);
	}

	&__grey-layer {
		margin-top: calc(map-get($space, l4) * -2);
		height: map-get($space, l6) * 2;
		background-color: map-get($color-grey, l4);
	}
}
