.best-coffee-sec {
	&__content {
		display: grid;
		grid-template-columns: 4fr 1fr;
		gap: map-get($space, l3);

		@include rwd-df(md) {
			grid-template-columns: 1fr;
		}
	}
	&__text {
	}
	&__image-container {
		background-image: url('../../assets/img/best-coffee/shadow.png');
		background-repeat: no-repeat;
		background-position: bottom center;
		padding-bottom: map-get($space, s3);

		@include rwd-df(md) {
			width: 50%;
			justify-self: center;
		}

		@include rwd-df(xs) {
			width: 70%;
		}

		img {
			border: map-get($space, s3) solid $color-white;
			@include boxShadow(main);
			border-radius: 50%;
		}
	}
}
