.coffee-card {
	$button-radius: map-get($space, l3);
	display: grid;
	grid-template-columns: 1fr 3fr $button-radius * 2;
	justify-items: center;
	gap: map-get($space, main);
	align-items: center;
	background-color: $color-white;
	padding: map-get($space, main);
	border-radius: map-get($space, s2);
	@include boxShadow(main);
	transition: all map-get($time, main);

	@include rwd-df(md) {
		grid-template-columns: 1fr;
	}

	&:hover {
		border-radius: 0;
	}

	&__image {
		height: map-get($space, l5) * 2;
		border-radius: map-get($space, s2);
		transition: all map-get($time, main);
		@include rwd-df(md) {
			height: map-get($space, l5) * 3;
		}
	}

	&:hover &__image {
		border-radius: 0;
	}

	&__text {
		@include rwd-df(md) {
			text-align: center;
		}
		&__title {
			transition: all map-get($time, main);
		}
		&__description {
		}
	}

	&:hover &__text__title {
		color: map-get($color-primary, main);
	}

	&__price {
		@include squareSize($button-radius * 2);
		font-size: map-get($font-size, l5);
		text-transform: capitalize;
		color: map-get($color-grey, l4);
		background-color: rgba(map-get($color-primary, d4), 0.98);
		border-radius: 50%;
		display: flex;
		justify-content: center;
		align-items: center;
		position: relative;
		transition: all map-get($time, main);

		&::before {
			content: '';
			display: block;
			@include absCenter;
			@include squareSize(calc($button-radius * 2 - 1.2rem));
			border-radius: 50%;
			border: map-get($space, s7) solid map-get($color-grey, l4);
			transition: all map-get($time, main);
		}

		&__value {
			sup {
				font-size: map-get($font-size, l2);
			}
		}
	}

	&:hover &__price {
		color: map-get($color-primary, main);

		&::before {
			border-color: map-get($color-primary, main);
		}
	}
}
