.scroll-to-top-btn {
	position: fixed;
	bottom: map-get($space, l1);
	#{$end}: map-get($space, l1);
	z-index: map-get($z-index, xxl1);
	border-radius: map-get($space, s2);

	padding: map-get($space, s4);
	background-color: map-get($color-grey, l2);
	color: map-get($color-primary, l5);
	font-size: map-get($font-size, l2);

	font-weight: map-get($font-weight, l3);
	line-height: 0;
	display: flex;
	justify-content: center;
	align-items: center;

	display: block;
	visibility: hidden;
	opacity: 0;
	pointer-events: none;
	transform: scale(0);
	transition: all map-get($time, l1);
	border: map-get($space, s7) solid map-get($color-primary, main);
	@include boxShadow(l3);

	.icon {
		@include squareSize(map-get($space, l2));
		border-radius: 50%;
		color: map-get($color-grey, l2);
		background-color: map-get($color-primary, d4);
	}

	&.show {
		visibility: visible;
		opacity: 0.7;
		transform: scale(1);
		pointer-events: auto;

		&:hover {
			opacity: 1;
			transform: translateY(map-get($space, s5) * -1);
		}

		&:focus {
			transform: translateY(map-get($space, s5) * 1);
		}
	}
}
