.contact-sec {
	&__content {
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		gap: map-get($space, l4);

		@include rwd-df(md) {
			grid-template-columns: 1fr;
		}
	}

	&__form {
	}

	&__map {
		border: map-get($space, s7) solid map-get($color-primary, main);
		border-radius: map-get($space, s6);
		@include boxShadow(main);
		height: map-get($space, l5) * 5;
		padding: map-get($space, s7);
		width: 100%;
	}
}
