.popular-card {
	$button-radius: map-get($space, l3);

	position: relative;
	background-image: url('../../assets/img/popular/popular-item-bg.png');
	background-position: center center;
	background-repeat: no-repeat;
	background-size: 100% 100%;
	padding: 0 map-get($space, s5);
	transition: all map-get($time, main);
	margin-bottom: $button-radius;

	&:hover {
		background-image: url('../../assets/img/popular//popular-item-bg-hover.png');
	}

	&__image {
		margin-#{$start}: -1px;
	}

	&__text {
		padding: map-get($space, l2) {
			bottom: $button-radius + map-get($space, l2);
		}
		text-align: center;
	}

	&__title {
		@extend .u-heading-font;
		font-size: map-get($font-size, l4);
		color: map-get($color-grey, l3);
		margin-bottom: map-get($space, l1);
		border-bottom: map-get($space, s8) solid currentColor;
		transition: all map-get($time, main);

		&::first-letter {
			text-transform: uppercase;
			font-size: map-get($font-size, l6);
		}
	}

	&:hover &__title {
		color: map-get($color-primary, main);
	}

	&__description {
		color: map-get($color-grey, main);
		font-size: map-get($font-size, main);
	}

	&__btn {
		@include squareSize($button-radius * 2);
		@extend .u-heading-font;
		font-size: map-get($font-size, l3);
		text-transform: capitalize;
		color: map-get($color-grey, l4);
		background-color: rgba(map-get($color-primary, d4), 0.98);
		border-radius: 50%;
		display: flex;
		justify-content: center;
		align-items: center;
		position: absolute;
		top: 100%;
		#{$start}: 50%;
		transform: translate(-50%, -50%);
		transition: all map-get($time, main);

		&::before {
			content: '';
			display: block;
			@include absCenter;
			@include squareSize(calc($button-radius * 2 - 1.2rem));
			border-radius: 50%;
			border: map-get($space, s7) solid map-get($color-grey, l4);
			transition: all map-get($time, main);
		}
	}

	&:hover &__btn {
		color: map-get($color-primary, main);

		&::before {
			border-color: map-get($color-primary, main);
		}
	}
}
