.image-card {
	position: relative;
	border: map-get($space, s3) solid $color-white;
	border-radius: map-get($space, s4);
	@include boxShadow(main);
	&__image {
		height: 100%;
		object-fit: cover;
	}
	&__text {
		position: absolute;
		#{$start}: 0;
		bottom: 0;
		width: 100%;
		padding: map-get($space, main);
		color: map-get($color-primary, l5);
		background-color: rgba(map-get($color-primary, d5), 0.9);
		&__title {
		}
		&__description {
		}
	}
}
