.contact-form {
	display: grid;
	gap: map-get($space, l1);

	@include rwd-df(md) {
		grid-template-columns: repeat(2, 1fr);
	}

	@include rwd-df(sm) {
		grid-template-columns: 1fr;
	}

	.input-controll {
		&--textarea {
		}

		&__input,
		&__textarea {
			display: block;
			width: 100%;
			font-size: map-get($font-size, l1);
			background-color: $color-white;
			padding: map-get($space, s2) map-get($space, main);
			border-radius: map-get($space, s3);
			border: map-get($space, s7) solid map-get($color-primary, main);
			transition: all map-get($time, main) ease;

			&:focus {
				box-shadow: rgba($color-black, 0.1) 0px 1rem 2rem;
				border-color: rgb(85, 197, 122);
				border-image: initial;
			}

			&:focus:invalid {
				border-color: rgb(255, 119, 48);
			}

			&:focus:invalid + label {
				display: block;
			}
		}

		&__label {
			display: none;
			color: rgba(red, 0.85);
			font-size: map-get($font-size, s1);
			font-weight: map-get($font-weight, l2);
			padding-#{$start}: map-get($space, main);
			margin-top: map-get($space, s2);
		}

		&__textarea {
			resize: none;
		}
	}
}
