.social-list {
	display: flex;
	flex-wrap: wrap;
	gap: map-get($space, s2);
	&__item {
		&__link {
			&:link,
			&:visited {
				display: block;
				border-radius: map-get($space, s4);
				overflow: hidden;
				background-color: map-get($color-grey, main);
				color: map-get($color-primary, l4);
			}
			&:hover,
			&:active {
				background-color: map-get($color-primary, main);
				color: map-get($color-primary, l5);
			}
			.icon {
				display: block;
				@include squareSize(3rem);
				background-color: inherit;
				color: inherit;
				padding: map-get($space, s4);
				transition: all map-get($time, main);
			}
		}
	}
}
